@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hero{height:100%;background:white;overflow:hidden}.hero__firstContainer{padding-top:20vh;margin-left:10%;width:400px}.hero__name{font-size:42px;font-family:"Montserrat",sans-serif}.hero__desc{margin:0;margin-bottom:5px;font-family:"Raleway",sans-serif;line-height:1.5}.hero__buttonContainer{margin-top:30px;display:flex;justify-content:flex-end}.hero__computerImage{z-index:100;height:35vw}.hero__test{content:'';position:absolute;right:0;bottom:-100vh;width:100%;height:150vh;background:#c334eb;transform:skewY(-40deg);overflow:hidden;z-index:1;box-shadow:0px 1px 10px grey}

.button{background:none;box-shadow:none;font-size:14px;font-family:"Raleway",sans-serif}.button:focus{outline:none}.button__website{border:1px solid lightgrey;padding:13px;border-radius:5px;transition:0.3s all ease}.button__website:hover{cursor:pointer;box-shadow:0px 0px 10px lightgrey}.button__website:active{box-shadow:0px 0px 3px lightgray}.button__app{border:none;display:flex;align-items:center;box-shadow:none;transition:all 0.3s ease;padding:10px;border-radius:5px}.button__app:hover{cursor:pointer;box-shadow:0px 0px 10px lightgrey}.button__app:active{box-shadow:0px 0px 3px lightgray}.button__arrow{height:20px;margin-left:5px;transition:all 0.3s ease}

.project{height:100%;background:white;overflow:hidden}.project__topContainer{position:absolute;left:0;right:0;height:100vh;width:100vw;z-index:10}.project__contentContainer{width:100%;height:100%;padding-top:20vh;display:flex;justify-content:center}.project__contentContainer--right{flex-direction:row-reverse}.project__image{height:400px}.project__name{font-size:42px;font-family:"Montserrat",sans-serif}.project__textContainer{width:400px;line-height:1.5;font-family:"Raleway",sans-serif}.project__buttonContainer{margin-top:30px;display:flex;justify-content:flex-end;align-items:center}.project__buttonWrapper{margin-left:20px}.project__slant-left{content:'';position:absolute;right:0;bottom:-100vh;width:100%;height:150vh;transform:skewY(40deg);overflow:hidden;z-index:1;box-shadow:1px 1px 10px grey}.project__slant-right{content:'';position:absolute;right:0;bottom:-100vh;width:100%;height:150vh;transform:skewY(-40deg);overflow:hidden;z-index:1;box-shadow:1px 1px 10px grey}

.contact{height:100%;background:white}.contact__topContainer{position:absolute;left:0;right:0;height:100vh;width:100vw;z-index:10;display:flex;justify-content:center;align-items:center}.contact__formContainer{width:500px;border:1px solid lightgray;border-radius:5px;box-shadow:0 0 10px grey;padding:20px;background:white}.contact__row{display:flex;margin-bottom:20px}.contact__textarea{resize:none}.contact__buttonContainer{display:flex;justify-content:flex-end;margin-top:20px}.contact__input{box-shadow:none;padding:10px 5px;border:1px solid lightgrey;border-radius:3px;box-shadow:none;transition:all 0.3s ease;font-size:16px;font-family:"Raleway",sans-serif;padding-left:10px}.contact__input:focus{box-shadow:0 0 10px lightgray;outline:none}.contact__input:hover{box-shadow:0 0 10px lightgray}.contact__inputContainer{display:flex;flex-direction:column;width:100%}.contact__label{margin-bottom:10px;font-size:18px;font-family:"Raleway",sans-serif}.contact__textArea{width:100%}.contact__textAreaContainer{display:flex;flex-direction:column;width:100%}.contact__slant-left{content:'';position:absolute;right:0;bottom:-100vh;width:100%;height:150vh;transform:skewY(40deg);overflow:hidden;z-index:1;box-shadow:1px 1px 10px grey}.contact__slant-right{content:'';position:absolute;right:0;bottom:-100vh;width:100%;height:150vh;transform:skewY(-40deg);overflow:hidden;z-index:1;box-shadow:1px 1px 10px grey}

